import React from 'react';
import { useSelector } from 'react-redux';

function PlayerOptions() {

  const language = useSelector((state) => state.language.language);

  return (
    <>
      <div className="question"><h4>{language==="en" ? "You are" : ""}</h4></div>
      <div className="container-for-players">
        <label><input type="radio" name="color" value="black" defaultChecked /> {language==="en" ? "First (Black)" : ""}</label>
        <label><input type="radio" name="color" value="white" /> {language==="en" ? "Second (White)" : ""}</label>
      </div>
    </>
  );
}

export default PlayerOptions;