import React from 'react';
import { useSelector } from 'react-redux';

function TurnAndScore() {
  const language = useSelector((state) => state.language.language);
  return (
    <div>
      <div className="turn subsection">
        <div className="info"><h4>{language==="en" ? "Turn" : ""}</h4></div>
        <div id="turn">{language==="en" ? "Black" : ""}</div>
      </div>
      <div className="score subsection">
        <div className="info"><h4>{language==="en" ? "Score" : ""}</h4></div>
        <div>{language==="en" ? "Black: " : ": "}<span id="black-score">2</span></div>
        <div>{language==="en" ? "White: " : ": "}<span id="white-score">2</span></div>
      </div>
    </div>
  );
}

export default TurnAndScore;
