// LoginModal.js
import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import Modal from 'react-modal';
import { useDispatch } from 'react-redux';
import { loginSuccess } from '../actions/authActions';
import { getCookie } from '../utilities';


const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    width: '400px', // 
    height: '300px', // 
  },
};

const LoginModal = ({ isOpen, onRequestClose }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const dispatch = useDispatch();
  const language = useSelector((state) => state.language.language);

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    // FormData 
    const formData = new FormData();
    formData.append('username', username);
    formData.append('password', password);
  
    // 
    const response = await fetch('/api/login/', {
      method: 'POST',
      headers: {
        'X-CSRFToken': getCookie('csrftoken'),
      },
      body: formData, // JSONformData
      credentials: 'include',
    });

    if (response.ok) { // response.status === 'success'response.ok
      const data = await response.json(); // 
      dispatch(loginSuccess(data.user)); // loginSuccess
      onRequestClose(); // 
      window.location.href = '/'; // 
    } else {
      setError('Failed Login. Please check your username and password.');
    }
  };
  

  return (
    <Modal 
    isOpen={isOpen} 
    onRequestClose={onRequestClose} 
    contentLabel="Login" 
    style={customStyles}>
      <h2>{language==="en" ? "Login" : ""}</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="username">{language==="en" ? "Username:" : ""}</label>
          <input
            id="username"
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>
        <div>
          <label htmlFor="password">{language==="en" ? "Password:" : ""}</label>
          <input
            id="password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <button type="submit">{language==="en" ? "Login" : ""}</button>
        {error && <p>{error}</p>}
      </form>
      <button onClick={onRequestClose}>{language==="en" ? "Close" : ""}</button>
    </Modal>
  );
};

export default LoginModal;
