import React from 'react';
import { useSelector } from 'react-redux';

function TotalResults() {

  const language = useSelector((state) => state.language.language);

  return (
    <div className="dashboard-element">
      <h3>{language==="en" ? "Total" : ""}</h3>
      <table id="ai-results-table-total" className="dashboard-table">
        <thead>
          <tr>
          <th>{language==="en" ? "AI Level" : "AI"}</th>
            <th>{language==="en" ? "Win" : ""}</th>
            <th>{language==="en" ? "Lose" : ""}</th>
            <th>{language==="en" ? "Draw" : ""}</th>
            <th>{language==="en" ? "Fastest Win" : ""}</th>
          </tr>
        </thead>
        <tbody>
          {/*  */}
        </tbody>
      </table>
    </div>
  );
}

export default TotalResults;